import Citys from '../city.js';
export default {
	components: {},
	props: {},

	computed: {
		hotCity() {
			return Citys.hotCity;
		},

		citys() {
			return Citys.cities;
		}
	},

	data() {
		return {
			statusBarHeight: this.statusBarHeight,
			ImgUrl: this.ImgUrl,
			letter: [],
			selectLetter: '',
			searchValue: '',
			scrollIntoId: '',
			list: [],
			tId: null,
			searchList: [],
			showMask: false,
			disdingwei: true,
			Visit: [], //最近访问
			position: '青岛',
			longitude: '', //经度
			latitude: '', //纬度
			seconds: 3,
			po_tips: '重新定位',
		}
	},

	created() {
		//获取存储的最近访问
		var that = this
		uni.getStorage({
			key: 'Visit_key',
			success: function(res) {
				that.Visit = res.data
			}
		});
		//获取定位 经度纬度
		that.getWarpweft()
		//获取city.js 的程序字母
		var mu = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'j', 'k', 'l', 'm', 'n', 'p', 'q', 'r', 's', 't', 'w', 'x', 'y',
			'z'
		];
		var tmp = [];
		for (var i = 0; i < mu.length; i++) {
			var item = mu[i];
			for (var j = 0; j < this.citys.length; j++) {
				var py = this.citys[j].py;
				if (py.substring(0, 1) == item) {
					if (tmp.indexOf(item) == -1) {
						this.list[i] = [this.citys[j]];
						tmp.push(item);
						this.letter.push(item.toUpperCase());
					} else {
						this.list[i].push(this.citys[j]);
					}
				}
			}
		}
	},
	methods: {
		getId(index) {
			return this.letter[index];
		},

		scrollTo(letter) {
			this.showMask = true
			this.selectLetter = letter == 'hot' ? '最' : letter
			setTimeout(() => {
				this.showMask = false
			}, 300);
			this.scrollIntoId = letter;
		},


		query(source, text) {
			let res = [];
			var self = this;
			res = source.filter(item => {
				const arr = [];
				let isHave = false;
				Object.keys(item).forEach(prop => {
					const itemStr = item[prop];
					self.isString(itemStr) &&
						itemStr.split(',').forEach(val => {
							arr.push(val);
						});
				});
				arr.some(val => {
					isHave = new RegExp('^' + text).test(val);
					return isHave;
				});
				return isHave;
			});
			console.log(JSON.stringify(res));
			return res;
		},

		isString(obj) {
			return typeof obj === 'string';
		},

		onInput(e) {
			const value = e.target.value;
			console.log(value);
			if (value !== '' && this.citys && this.citys.length > 0) {
				const queryData = this.query(this.citys, String(value).trim());
				this.searchList = queryData;
				this.disdingwei = false
			} else {
				this.searchList = [];
				this.disdingwei = true
			}
		},

		back_city(item) {
			if (item) {
				this.$emit('back_city', item);
				//unshift 把数据插入到首位，与push相反
				this.Visit.unshift(item)
				this.searchValue = "";
				this.disdingwei = true
				var arr = this.Visit
				//数组去重
				function distinct(arr) {
					let newArr = []
					for (let i = 0; i < arr.length; i++) {
						if (newArr.indexOf(arr[i]) < 0) {
							newArr.push(arr[i])
						}
					}
					return newArr
				}
				this.Visit = distinct(arr)
				console.log(this.Visit, "---最近访问")
				uni.setStorage({
					key: 'Visit_key',
					data: this.Visit
				});
			} else {
				this.$emit('back_city', 'no');
			}

		},
		getWarpweft() {
			var that = this
			that.po_tips = '定位中...'
			let countdown = setInterval(() => {
				that.seconds--;
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log('当前位置的经度：' + res.longitude);
						console.log('当前位置的纬度：' + res.latitude);
						that.longitude = res.longitude
						that.latitude = res.latitude
					}
				});
				if (that.seconds <= 0) {
					that.seconds = 3
					that.po_tips = '重新定位'
					clearInterval(countdown);
				}
			}, 1000);
		}
	}
};
